.footer { background-color: #282c34; padding: 20px 0; color: white; text-align: center; position: fixed; bottom: 0; width: 100%; }
.footer-content { max-width: 1200px; margin: 0 auto; display: flex; flex-direction: column; align-items: center; }
.footer-links { list-style: none; padding: 0; margin: 10px 0 0 0; display: flex; gap: 15px; }
.footer-links li { display: inline; }
.footer-links a { color: #61dafb; text-decoration: none; }
.footer-links a:hover { text-decoration: underline; }

